.sequenceWidget {
  display: flex;
  flex-direction: column;
  border: 1px solid #ccc;
  background-color: #fff;
  padding: 10px;
  flex: 1;
}

.sequenceWidgetLabels {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.indicatorLabel,
.currentWordLabel,
.difficultyLabel {
  padding: 5px;
  border: 1px solid #ddd;
  background-color: #f9f9f9;
}

.sequenceWidgetMain {
  display: flex;
  flex: 1;
}

.scrollArea {
  flex: 4;
  border: 1px solid #ddd;
  background-color: #f4f4f4;
  padding: 10px;
  overflow-y: auto;
}

.buttonPanel {
  flex: 1;
  border: 1px solid #ddd;
  background-color: #f9f9f9;
  padding: 10px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.graphEditor {
  margin-top: 10px;
  border: 1px solid #ddd;
  background-color: #f9f9f9;
  padding: 10px;
}
